import React from "react";
import { TextField, Button, Link, Typography, InputAdornment } from "@mui/material";
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import useToggle from "../../hooks/useToggle";
const inputStyle = {
     margin: "5px",
     width: "100%",
     height: "30%"
}
const cursorPointer = { cursor: "pointer" }
export default function ForgottenPassword({ setEmail, back, submit, isSubmited, setIsSubmited, setPassword1, setPassword2, password1, password2, email, setCode, code, changePassword }) {

     const [isPassword1Visible, setIsPassword1Visible] = useToggle(false)
     const [isPassword2Visible, setIsPassword2Visible] = useToggle(false)
     return (
          <div className="Form">
               <div className="Inputs">
                    <Typography variant="button" fontSize={"1rem"}> </Typography>
                    {isSubmited ?
                         <>
                              <TextField disabled style={inputStyle} label=" " type="text" value={email} variant="outlined"
                              />
                              <TextField style={inputStyle} label="" type="text" variant="outlined" onChange={setCode}
                                   value={code}
                                   helperText="       "
                              />
                              <TextField
                                   style={inputStyle}
                                   label="   "
                                   type={isPassword1Visible ? "text" : "password"}
                                   variant="outlined"
                                   onChange={setPassword1}
                                   value={password1}
                                   InputProps={{
                                        startAdornment:
                                             <InputAdornment position="start">
                                                  {isPassword1Visible ?
                                                       <VisibilityOffIcon
                                                            style={cursorPointer}
                                                            onClick={setIsPassword1Visible}
                                                       />
                                                       :
                                                       <RemoveRedEyeIcon
                                                            style={cursorPointer}
                                                            onClick={setIsPassword1Visible}
                                                       />
                                                  }
                                             </InputAdornment>

                                   }}
                              />
                              <TextField
                                   style={inputStyle}
                                   label="   "
                                   type={isPassword2Visible ? "text" : "password"}
                                   variant="outlined"
                                   onChange={setPassword2}
                                   value={password2}
                                   InputProps={{
                                        startAdornment:
                                             <InputAdornment position="start">
                                                  {isPassword2Visible ?
                                                       <VisibilityOffIcon
                                                            style={cursorPointer}
                                                            onClick={setIsPassword2Visible}
                                                       />
                                                       :
                                                       <RemoveRedEyeIcon
                                                            style={cursorPointer}
                                                            onClick={setIsPassword2Visible}
                                                       />
                                                  }
                                             </InputAdornment>

                                   }}
                              />
                              <Button style={inputStyle} variant="contained" onClick={changePassword} ></Button>
                              <Link className="Link" underline="none" onClick={setIsSubmited}>
                                   {''}
                              </Link>
                         </>
                         :
                         <>
                              <TextField style={inputStyle} label="   " type="text" variant="outlined" onChange={setEmail} value={email}
                              />
                              <Button style={inputStyle} variant="contained" onClick={submit} ></Button>
                              <Link className="Link" underline="none" onClick={back}>
                                   {''}
                              </Link>
                         </>
                    }
               </div>
          </div>
     )
}