import React, { useEffect, useState } from "react";
import { TextField, Button } from "@mui/material";

//Styling for the whole component is done in Setings.js & Settings.css except for the styling writen inline

export default function CompanyInfo({ inputsStyle, user, submit, flashPopUp }) {
     const [companyName, setCompanyName] = useState("")
     const [address, setAddress] = useState("")
     const [tel, setTel] = useState("")
     const [bankAccount, setBankAccount] = useState("")
     const [taxNumber, setTaxNumber] = useState("")
     const [image, setImage] = useState()
     const handleSubmit = () => {
          const data = { companyName, address, tel, bankAccount, taxNumber }
          submit(data, image)
     }
     const handleImageUpload = (e) => {
          if (e.target.files[0]) {
               if (e.target.files[0].size > 5000000) {
                    flashPopUp("error", "       5mb")
               } else if (!(e.target.files[0].type === "image/png" || e.target.files[0].type === "image/jpeg")) {
                    flashPopUp("error", " ,    .jpeg, .jpg  .png ")
               } else {
                    setImage(e.target.files[0])
               }
          }
     }
     useEffect(() => {

          setCompanyName(user.companyName)
          setAddress(user.address)
          setTel(user.tel)
          setBankAccount(user.bankAccount)
          setTaxNumber(user.taxNumber)

     }, [user])
     return (
          <div className="InputRows">
               <div className="InputGroups">
                    <TextField variant="outlined" label="   " value={companyName} onChange={(e) => setCompanyName(e.target.value)} type="text" fullWidth style={inputsStyle} />
                    <TextField variant="outlined" label="" value={address} onChange={(e) => setAddress(e.target.value)} type="text" fullWidth style={inputsStyle} />
                    <TextField variant="outlined" label=" " value={tel} onChange={(e) => setTel(e.target.value)} type="text" fullWidth style={inputsStyle} />


               </div>
               <div className="InputGroups">
                    <TextField variant="outlined" label=" " value={bankAccount} onChange={(e) => setBankAccount(e.target.value)} type="text" fullWidth style={inputsStyle} />
                    <TextField variant="outlined" label=" " value={taxNumber} onChange={(e) => setTaxNumber(e.target.value)} type="text" fullWidth style={inputsStyle} />
                    <div className="ChangeLogoInput">
                         <TextField variant="outlined" fullWidth type="file" onChange={handleImageUpload}
                              helperText="   " style={inputsStyle} encType="multipart/form-data" />

                         {user.logo.path && <img src={user.logo.path} alt="   " style={{ width: "20%", height: "80%" }} />}
                    </div>
                    <Button variant="contained" onClick={handleSubmit} fullWidth></Button>

               </div>
          </div>
     )
}